* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
}

:root {
  --font-family-primary: "Roboto", sans-serif;
  --font-family-secondary: Roboto, sans-serif;
  --font-weight-light: 300;
  --font-weight-regular: 400;
  --font-weight-medium: 500;
  --font-weight-bold: 700;
  --color-primary: #111;
  --color-secondary: #f87719;
  --color-tertiary: #b7b7b7;
  --font-size-title: 28px;
  --font-size-subtitle: 20px;
  --font-size-description: 16px;
  --font-size-rating: 15px;
  --text-primary-color: #f8f8f8;
  --text-secondary-color: #fff;
  --btn-linear-color: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  --primary-duration-timing: 250ms cubic-bezier(.4, 0, .2, 1);
}

body.light {
  --color-primary: #fff;
  --color-secondary: #f87719;
  --color-tertiary: #595959;
  --text-primary-color: #111;
  --text-secondary-color: #111;
  background-color: #fff;
  transition: all .3s;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
}

body {
  background-color: #111;
  transition: all .3s;
}

p:last-child {
  margin-top: 0;
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

@media screen and (min-width: 320px) {
  .container {
    min-width: 320px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    min-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    min-width: 1280px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

section {
  font-family: Roboto, sans-serif;
}

.list {
  list-style-type: none;
}

.link {
  list-style: none;
}

.ishidden {
  display: none;
}

