.container {
  max-width: 1280px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  padding-top: 8px;
  padding-bottom: 8px;
  display: flex;
}

.logo-group {
  display: flex;
}

.logo {
  z-index: 20;
  display: block;
}

.site-name {
  color: var(--text-secondary-color);
  cursor: pointer;
  align-items: center;
  font-size: 24px;
}

.site-name:hover, .site-name:focus {
  color: orange;
}

.menu {
  display: block;
}

.menu__list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.menu-item {
  color: gray;
}

.menu-item:hover, .menu-item:focus {
  color: orange;
}

.menu-link {
  font-size: 14px;
}

#themetoggle {
  cursor: pointer;
  background-color: #fff;
  border: none;
  padding: 0;
}

.button-icon {
  background-color: var(--color-primary);
  z-index: 20;
  width: 20px;
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 10;
  background-color: rgba(145, 145, 145, .2);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  display: none;
}

.active {
  color: orange;
}

@media screen and (min-width: 768px) {
  .menu {
    margin-right: 417px;
  }
}

@media screen and (min-width: 480px) {
  .header__container {
    justify-content: space-between;
    display: flex;
  }

  .logo {
    margin-right: 8px;
  }

  .site-name {
    margin-right: 30px;
  }

  .menu {
    margin-right: 20px;
    display: flex;
  }

  .menu__list {
    display: flex;
  }

  .menu-item:not(:last-child) {
    margin-bottom: 0;
    margin-right: 15px;
  }

  .menu-title {
    display: none;
  }
}

@media screen and (max-width: 479px) {
  .header__container {
    justify-content: space-between;
  }

  .site-name {
    display: none;
  }

  .menu-title {
    color: var(--color-tertiary);
    background-color: rgba(0, 0, 0, 0);
    border-color: rgba(0, 0, 0, 0);
    margin-left: 40px;
    padding-top: 0;
    padding-bottom: 0;
    display: block;
  }

  .active {
    color: orange;
  }

  .hidden {
    visibility: hidden;
  }

  .menu {
    z-index: 30;
    width: 204px;
    background-color: var(--color-primary);
    height: 100vh;
    padding: 80px 105px 50px 20px;
    transition-duration: .5s;
    transition-timing-function: linear;
    display: block;
    position: absolute;
    top: 0;
    left: -204px;
  }

  .menu__list {
    display: block;
  }

  .menu-link {
    color: var(--text-secondary-color);
    font-size: 14px;
  }

  .menu-link:hover, .menu-link:focus, .menu-link.active {
    color: orange;
  }

  .header.open .menu {
    transform: translateX(100%);
  }

  .menu-item:not(:last-child) {
    margin-bottom: 24px;
  }
}

.blocked-element {
  pointer-events: none;
}

.noScroll {
  overflow: hidden;
}

.hero {
  max-width: 1280px;
  height: 380px;
  background-color: #fbf2f2;
  background-image: linear-gradient(86.77deg, #111 1.38%, rgba(17, 17, 17, 0) 10.61%), url("mobile-bg-home-x1-min.c9687df9.jpg");
  background-position: 90px;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  margin: 0 auto;
  padding-left: 20px;
  position: relative;
}

.hero-title {
  max-width: 200px;
  color: #fff;
  padding-top: 40px;
  font-size: 32px;
  font-style: normal;
  line-height: 1.1875;
}

.hero-text {
  max-width: 180px;
  color: #fff;
  margin-top: 8px;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.14286;
}

.hero-wrapper-btn {
  gap: 10px;
  margin-top: 20px;
  display: flex;
}

.hero-getstarted-btn {
  color: #111;
  background: var(--btn-linear-color);
  cursor: pointer;
  transition: var(--primary-duration-timing);
  border: 1px solid;
  border-radius: 74px;
  padding: 12px 24px;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.16667;
}

.hero-getstarted-btn:hover, .hero-getstarted-btn:focus {
  color: #f87719;
  background: #fff;
  border: 1px solid #f87719;
}

.hero-moredetails-btn {
  color: #fff;
  cursor: pointer;
  transition: var(--primary-duration-timing);
  background: none;
  border: 1px solid #fff;
  border-radius: 74px;
  padding: 12px 24px;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.16667;
}

.hero-moredetails-btn:hover, .hero-moredetails-btn:focus {
  color: #f87719;
  background: #fff;
  border: 1px solid #f87719;
}

.trailer-modal {
  max-width: 480px;
  max-height: 360px;
  width: 90%;
  height: 80%;
  background-color: #111;
  border-radius: 16px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 1px 1px 14px 4px rgba(255, 107, 8, .42);
}

.trailer-text {
  color: var(--text-secondary-color);
  padding: 39px 33px 0;
  font-size: 16px;
  line-height: 1.1875;
}

.trailer-srcset {
  justify-content: center;
  display: flex;
}

.trailer-img {
  margin-top: 24px;
}

.trailer-svg-close {
  width: 7px;
  height: 7px;
  cursor: pointer;
  z-index: 2;
  position: absolute;
  top: 24px;
  right: 24px;
}

.trailer-svg-close.inother-position {
  width: 10px;
  height: 10px;
  top: -14px;
  right: 50%;
  transform: translate(-50%, -50%);
}

#trailer-video {
  max-width: 480px;
  max-height: 360px;
  width: 100%;
  height: 100%;
  z-index: 1;
  border-radius: 16px;
  position: absolute;
}

.modal-overlay {
  z-index: 2;
  background-color: rgba(0, 0, 0, .5);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: scroll;
}

.hero-more-deteils {
  width: 90%;
  min-width: 320px;
  background-color: var(--color-primary);
  z-index: 3;
  border-radius: 16px;
  margin-top: 60px;
  padding: 52px 16px;
  position: absolute;
  left: 50%;
  transform: translate(-50%);
  box-shadow: 1px 1px 14px 4px rgba(255, 107, 8, .42);
}

.hero-more-poster {
  height: 500px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.hero-more-svg {
  color: var(--text-secondary-color);
  cursor: pointer;
  position: absolute;
  top: 20px;
  right: 20px;
}

.hero-more-svg:hover {
  color: #f87719;
}

.hero-more-title {
  color: var(--text-secondary-color);
  margin-top: 22px;
  font-size: 20px;
  font-weight: 500;
  line-height: 23px;
}

.hero-more-container {
  justify-content: space-between;
  margin-top: 30px;
  display: flex;
}

.hero-more-wrapper {
  flex-direction: column;
  gap: 12px;
  display: flex;
}

.hero-more-box {
  width: 50%;
  flex-direction: column;
  gap: 12px;
  display: flex;
}

.hero-more-specification {
